import { BaseElement } from "./BaseElement";
import { BpmnModel } from "./BpmnModel";
import { BpmnModelHelper } from "./BpmnModelHelper";
import { Expression } from "./Expression";

export class ResourceAssignmentExpression extends BaseElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    Expression: Expression;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Expression = null;
    }

    ConvertToJsonObject(): any {
        let obj: any = super.ConvertToJsonObject();

        if (this.Expression != null) {
            BpmnModelHelper.SetAttributeValue(obj, "Expression", this.Expression.ConvertToJsonObject());
        }

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        if (obj.hasOwnProperty("Expression")) {
            this.Expression = new Expression(this.Model);
            this.Expression.LoadFromJsonObject(obj.Expression);
        }
    }
}